import logo from './logo.svg';
import './App.css';


function App() {

  function changetheme() {


    document.querySelector("body").style.backgroundColor = "red"

  }

  return (

    <div id="container" className="container">

      <h2 id='head'   > <span >Calc</span>   <button onClick={changetheme}     >Theme</button> </h2>

      <div id="display"></div>

      <div id="btn-sec">

        <p> <button

          onClick={() => {

            document.getElementById("display").innerHTML += "7";

          }}

        >7</button>
          <button onClick={() => {

            document.getElementById("display").innerHTML += "8";

          }}>8</button>
          <button onClick={() => {

            document.getElementById("display").innerHTML += "9";

          }}   >9</button>
          <button


            onClick={() => {
              let x = document.getElementById("display").innerHTML.length;
              let y = "";
              for (let i = 0; i < x - 1; i++) {
                y = y + document.getElementById("display").innerHTML.charAt(i);
              }
              document.getElementById("display").innerHTML = y;
            }}


          >DEL</button>  </p>

        <p> <button onClick={() => {

          document.getElementById("display").innerHTML += "4";

        }}  >4</button>
          <button onClick={() => {

            document.getElementById("display").innerHTML += "5";

          }}>5</button>
          <button onClick={() => {

            document.getElementById("display").innerHTML += "6";

          }}>6</button>
          <button

            onClick={() => {

              document.getElementById("display").innerHTML += "+";

            }}

          >+</button>  </p>

        <p> <button onClick={() => {

          document.getElementById("display").innerHTML += "1";

        }}>1</button>
          <button onClick={() => {

            document.getElementById("display").innerHTML += "2";

          }}>2</button>
          <button onClick={() => {

            document.getElementById("display").innerHTML += "3";

          }}  >3</button>
          <button onClick={() => {

            document.getElementById("display").innerHTML += "-";

          }} >-</button>  </p>

        <p> <button onClick={() => {

          document.getElementById("display").innerHTML += ".";

        }}>.</button>
          <button onClick={() => {

            document.getElementById("display").innerHTML += "0";

          }}>0</button>
          <button onClick={() => {

            document.getElementById("display").innerHTML += "/";

          }}>/</button>
          <button onClick={() => {

            document.getElementById("display").innerHTML += "*";

          }}>x</button>  </p>

        <p> <button onClick={() => {

          document.getElementById("display").innerHTML = "";

        }}>RESET</button>
          <button

            onClick={() => {

              let x = document.getElementById("display");
              let y = eval(x.innerHTML);

              document.getElementById("display").innerHTML = y;

            }}

          >=</button>
        </p>

      </div>

    </div>
  );
}

export default App;
